import { atomWithStorageForSpa } from '@/atoms/utils-atom'

import type { RepoIdentifier, ViewType } from './types'

// GitHub token atom - persisted to localStorage
export const githubTokenAtom = atomWithStorageForSpa<string | undefined>('githubToken', undefined)

export const DEFAULT_REPO_ID: RepoIdentifier = {
  owner: 'flash-man-9d6a2',
  repo: 'default',
}

export type FlashManConfig = {
  viewType: ViewType
  sortByDateDesc: boolean
  repos: RepoIdentifier[]
}

export const flashManConfigAtom = atomWithStorageForSpa<FlashManConfig>('flashManConfig', {
  viewType: 'list',
  sortByDateDesc: false,
  repos: [],
})

// Learn configuration types
export type Language = 'ko-KR' | 'en-US' | 'ja-JP'
export type QaDirection = 'forward' | 'reverse'
export type QaType = 'one-from-many' | 'short-answer'

export type LearnConfig = {
  shuffle: boolean
  qaDirection: QaDirection
  qaType: QaType
  hideHint: boolean
  soundOnLearning: boolean
  ttsForQuestion: boolean
  ttsForAnswer: boolean
  ttsLang: Language
  passRatio: number
}

export const learnConfigAtom = atomWithStorageForSpa<LearnConfig>('learnConfig', {
  shuffle: false,
  qaDirection: 'forward',
  qaType: 'one-from-many',
  hideHint: false,
  soundOnLearning: true,
  ttsForQuestion: false,
  ttsForAnswer: false,
  ttsLang: 'ko-KR',
  passRatio: 100,
})
