import { Octokit } from 'octokit'

import type {
  ContentIdentifier,
  ContentItem,
  ContentTreeItem,
  FlashCardContent,
  FlashManJson,
  OwnerIdentifier,
  RepoIdentifier,
  Repository,
} from '../types'

export function makeOctokit(accessToken?: string): Octokit {
  return new Octokit(accessToken ? { auth: accessToken } : {})
}

export async function getOrgRepositories(
  octokit: Octokit,
  { owner }: OwnerIdentifier
): Promise<Repository[]> {
  const resp = await octokit.rest.repos.listForOrg({
    org: owner,
    type: 'all',
  })
  return resp.data
}

export async function getUserRepositories(
  octokit: Octokit,
  { owner }: OwnerIdentifier
): Promise<Repository[]> {
  const resp = await octokit.rest.repos.listForUser({
    username: owner,
    sort: 'updated',
  })
  return resp.data
}

export async function getAuthenticatedUserRepositories(octokit: Octokit, query?: string) {
  const resp = await octokit.rest.repos.listForAuthenticatedUser({
    sort: 'updated',
    // type: 'all',
    affiliation: 'owner,collaborator,organization_member',
    visibility: 'all',
    per_page: 1000,
    query,
  })
  // console.log('getAuthenticatedUserRepositories', resp)
  return resp.data
}

export async function getContents(
  octokit: Octokit,
  repoIdentifier: RepoIdentifier,
  path: string
): Promise<ContentItem | ContentItem[]> {
  try {
    const resp = await octokit.rest.repos.getContent({
      ...repoIdentifier,
      path,
    })
    return resp.data as ContentItem | ContentItem[]
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error(e)
  }
  return []
}

export async function getContentTree(
  octokit: Octokit,
  repoIdentifier: RepoIdentifier,
  path: string
): Promise<ContentTreeItem[]> {
  const contents = await getContents(octokit, repoIdentifier, path)
  const ret =
    contents instanceof Array
      ? await Promise.all(
          contents.map(async (content) => {
            if (content.type === 'dir') {
              return getContentTree(octokit, repoIdentifier, content.path).then((children) => ({
                content,
                children,
              }))
            }
            return { content }
          })
        )
      : []
  return ret.filter(({ content }) => {
    const { type, name } = content
    if (name.startsWith('.')) {
      return false
    }
    return (
      type === 'dir' || name.endsWith('.md') || name.endsWith('.txt') || name.endsWith('.fm.json')
    )
  })
}

function b64DecodeUnicode(str: string): string {
  if (typeof window !== 'undefined' && 'atob' in window && 'decodeURIComponent' in window) {
    return decodeURIComponent(
      Array.prototype.map
        .call(atob(str), (c: string) => {
          return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
        })
        .join('')
    )
  }
  return ''
}

export async function getFlashCardContent(
  octokit: Octokit,
  repoIdentifier: RepoIdentifier,
  path: string
): Promise<FlashCardContent | undefined> {
  try {
    const resp = await octokit.rest.repos.getContent({
      ...repoIdentifier,
      path,
    })
    const contentItem = resp.data as ContentItem
    const content = b64DecodeUnicode((contentItem as unknown as { content: string })?.content || '')

    if (path.endsWith('.fm.json')) {
      const flashManJson = JSON.parse(content) as FlashManJson
      return {
        ...contentItem,
        flashCardItems: flashManJson.items.map(({ question, answer }, i) => ({
          q: question,
          a: answer,
          lineNo: i,
        })),
      }
    }

    const lines = content.split('\n')
    const flashCardItems = lines
      .map((l, id) => {
        const [q, a] = l.replace(/\t+/, '\t').split('\t')
        return { lineNo: id, q, a: a?.replace(/^\| /, '') }
      })
      .filter(({ q, a }) => Boolean(q) && Boolean(a))

    return {
      ...contentItem,
      flashCardItems,
    }
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error(e)
  }
}

function countFmFiles(tree: ContentTreeItem[]): number {
  return tree.reduce((count, item) => {
    if (
      item.content.type === 'file' &&
      (item.content.name.endsWith('.fm.json') || item.content.name.endsWith('.fm.md'))
    ) {
      return count + 1
    }
    return count + (item.children ? countFmFiles(item.children) : 0)
  }, 0)
}

export async function countFlashManFiles(
  octokit: Octokit,
  repoIdentifier: RepoIdentifier,
  path: string
): Promise<number> {
  const tree = await getContentTree(octokit, repoIdentifier, path)
  return countFmFiles(tree)
}

function makeQuery(obj: Record<string, unknown>): string {
  const params = new URLSearchParams()
  for (const [key, value] of Object.entries(obj)) {
    if (value !== undefined && value !== null && value !== '') {
      params.set(key, String(value))
    }
  }
  const ret = params.toString()
  return ret ? `?${ret}` : ''
}

export async function getLastCommit(
  octokit: Octokit,
  { owner, repo, path }: ContentIdentifier
): Promise<{ commit: { author: { date: string } } } | undefined> {
  try {
    const resp = await octokit.request({
      url: `/repos/${owner}/${repo}/commits${makeQuery({ path, per_page: 1 })}`,
    })
    return resp.data[0]
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error(e)
  }
}
