import { useCallback, useEffect, useMemo } from 'react'

import { useSuspenseQuery } from '@tanstack/react-query'
import { useAtom } from 'jotai'
import { Octokit } from 'octokit'
import { useNavigate } from 'react-router-dom'

import { useAuth } from '@/contexts/AuthContext'
import { routes } from '@/routes'

import { DEFAULT_REPO_ID, githubTokenAtom } from './atoms'
import {
  getAuthenticatedUserRepositories,
  getContents,
  getContentTree,
  getLastCommit,
  makeOctokit,
} from './lib/github'
import type { ContentIdentifier, ContentItem, ContentTreeItem, RepoIdentifier } from './types'

/**
 * Hook to get and persist the GitHub token.
 * When session token is available, it's saved to persistent storage.
 * Returns the token from session if available, otherwise from storage.
 */
export function useGithubToken(): string | undefined {
  const { session } = useAuth()
  const [storedToken, setStoredToken] = useAtom(githubTokenAtom)

  const sessionToken = session?.provider_token

  // console.log('useGithubToken', { storedToken, sessionToken })

  // Save session token to persistent storage when available
  useEffect(() => {
    if (sessionToken && sessionToken !== storedToken) {
      setStoredToken(sessionToken)
    }
  }, [sessionToken, storedToken, setStoredToken])

  // Return session token if available, otherwise use stored token
  return sessionToken ?? storedToken
}

/**
 * Hook to get an Octokit instance with the current GitHub token.
 * The token is automatically persisted and reused across sessions.
 */
export function useOctokit(): Octokit {
  const token = useGithubToken()
  return useMemo(() => makeOctokit(token), [token])
}

export function useActiveContent(contentId: ContentIdentifier) {
  const { repo, owner } = contentId
  const navigate = useNavigate()

  const setActiveContent = useCallback(
    (item: ContentItem) => {
      const { path, type } = item
      if (type === 'file') {
        // Navigate to cards view for files
        navigate(routes.flashMan.cards.$({ owner, repo, path }))
      } else {
        // Navigate to contents view for directories
        navigate(routes.flashMan.contents.$({ owner, repo, path }))
      }
    },
    [navigate, owner, repo]
  )

  return { setActiveContent }
}

function findPathShaList(tree: ContentTreeItem[], path: string): string[] | undefined {
  for (const item of tree) {
    const { content, children } = item
    if (content.path === path) {
      return [content.sha]
    }
    if (children) {
      const shaList = findPathShaList(children, path)
      if (shaList) {
        return [content.sha, ...shaList]
      }
    }
  }
}

export function useContentTree(contentIdentifier?: ContentIdentifier): {
  contentTree: ContentTreeItem[]
  shaList?: string[]
} {
  const octokit = useOctokit()
  const token = useGithubToken()
  const { owner, repo, path } = contentIdentifier || { owner: '', repo: '', path: '' }

  // console.log('useContentTree', { token, owner, repo, path })
  const { data } = useSuspenseQuery({
    queryKey: ['contentTree', owner, repo, token],
    queryFn: async () => {
      if (!owner || !repo) {
        return { contentTree: [], shaList: undefined }
      }
      const contentTree = await getContentTree(octokit, { owner, repo }, '')
      const shaList = path ? findPathShaList(contentTree, path) : undefined
      return { contentTree, shaList }
    },
    staleTime: 1000 * 60 * 5, // 5 minutes
  })

  return data || { contentTree: [], shaList: undefined }
}

export function useContentsEx(contentIdentifier: ContentIdentifier) {
  const octokit = useOctokit()
  const token = useGithubToken()
  const { owner, repo, path } = contentIdentifier

  // console.log('useContentsEx', { token, owner, repo, path })
  const { data } = useSuspenseQuery({
    queryKey: ['contentsEx', owner, repo, path, token],
    queryFn: async () => {
      if (!owner || !repo) {
        return []
      }
      const contents = await getContents(octokit, { owner, repo }, path || '')

      if (contents instanceof Array) {
        const lastCommits = await Promise.all(
          contents.map(({ path }) => getLastCommit(octokit, { owner, repo, path }))
        )
        contents.forEach((content, i) => {
          content.lastModified = lastCommits[i]?.commit.author.date
        })
      }
      return contents
    },
    staleTime: 1000 * 60 * 5, // 5 minutes
  })

  return data || []
}

export type RepositoryListItem = {
  label: string
  owner: string
  repo: string
}

export function useRepositoryList(
  configRepos: RepoIdentifier[],
  query?: string
): RepositoryListItem[] {
  const octokit = useOctokit()
  const token = useGithubToken()

  // console.log('useRepositoryList', { query })
  const { data: githubRepos } = useSuspenseQuery({
    queryKey: ['useRepositoryList', token, query],
    queryFn: async () => {
      if (!token) {
        return []
      }
      const repos = await getAuthenticatedUserRepositories(octokit, query)
      // console.log('useRepositoryList', repos)
      return repos.map(({ owner, name }) => ({
        owner: owner?.login || '',
        repo: name,
      }))
    },
    staleTime: 1000 * 60 * 5, // 5 minutes
  })

  return useMemo(() => {
    const allRepos = [DEFAULT_REPO_ID, ...configRepos, ...(githubRepos || [])]
    // Deduplicate by owner/repo
    const seen = new Set<string>()
    return allRepos
      .filter(({ owner, repo }) => {
        const key = `${owner}/${repo}`
        if (seen.has(key)) return false
        seen.add(key)
        return true
      })
      .map(({ owner, repo }) => ({
        label: `${owner}/${repo}`,
        owner,
        repo,
      }))
  }, [configRepos, githubRepos])
}
